import React, { Component } from 'react';
import Record from './Record';
import * as RecordsAPI from '../utils/RecordsAPI'
import RecordForm from './RecordForm';
import AmountBox from './AmountBox';


export default class Records extends Component {
    constructor() {
        super();
        this.state = {
            error: null,
            isLoaded: false,//
            records: []
        }
    }
    componentDidMount() {
        //axios.get("https://5b56f6f688d93a0014b024ab.mockapi.io/api/v1/records")
        RecordsAPI.getAll()
            .then(
                response => this.setState({
                    records: response.data,
                    isLoaded: true//
                })
            )
            .catch(
                error => this.setState({
                    error,
                    // keyvalueerror:error
                    isLoaded: true
                })
            )
    }
    addRecord(record) {
        this.setState({
            error: null,
            isLoaded: true,//
            records: [
                ...this.state.records,//
                record//
            ]
        })
    }
    //https://cloud.tencent.com/developer/section/1374244
    updateRecord(record, data) {//
        const recordIndex = this.state.records.indexOf(record);
        const newRecords = this.state.records.map((item, index) => {
            if (index !== recordIndex) {
                // This isn't the item we care about - keep it as-is
                return item;
            }
            // Otherwise, this is the one we want - return an updated value
            return {
                ...item,
                ...data
            };
        });
        this.setState({
            records: newRecords
        })
    }
    deleteRecord(record){//id
            const recordIndex = this.state.records.indexOf(record);
            const newRecords = this.state.records.filter( (item, index) => index !== recordIndex);
            this.setState({
                records:newRecords
            })
        }
    credit(){
        let credits = this.state.records.filter((record)=>{
            return record.amount>=0;
        })
        return credits.reduce((prev,curr)=>{
            return prev+Number.parseInt(curr.amount,0)
        },0)//reduce()  
    }
    debit(){
        let debits = this.state.records.filter((record)=>{
            return record.amount<0;
        })
        return debits.reduce((prev,curr)=>{
            return prev+Number.parseInt(curr.amount,0)
        },0)//reduce()  
    }
    balance(){
        return this.credit()+this.debit()
    }
    render() {
        //es6
        const { error, isLoaded, records } = this.state;
        if (error) {
            return <div>Error:{error.message}</div>
        }
        else if (!isLoaded) {
            return <div>Loading......</div>
        } else {
            return (
                <div>
                    <div className="row mb-3">
                    <AmountBox text="Credit" type="success" amount={this.credit()}/>
                    <AmountBox text="Debit" type="danger" amount={this.debit()}/>
                    <AmountBox text="Balance" type="success" amount={this.balance()}/>
                    </div>
                    <RecordForm handleNewRecord={this.addRecord.bind(this)} />
                    <table className="table table-bordered">
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {/* {...record}record={record}record.jsthis.props.datethis.props.record.date */}
                            { records.map((record) =>(//
                                <Record 
                                    key={record.id} 
                                    record={record} 
                                    handleEditRecord={this.updateRecord.bind(this)} 
                                    handleDeleteRecord={this.deleteRecord.bind(this)}   
                                />
                               ))}
                        </tbody>
                    </table>

                </div>
            );
        }
    }
}



